/************ FOOTER SECTION ************/
@keyframes footer-in {
    0% {
        transform: translate(500px, 10px) rotateZ(10deg);
    }
    50% {
        transform: translate(20px, 15px) rotateZ(-4deg);
    }
    100% {
        transform: translate(0px, 0px) rotateZ(0deg);
    }
}
@keyframes footer-robot {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotateX(14deg);
    }
}
@keyframes footer-air {
    from {
        transform: rotateX(0deg);
        opacity: 0.9;
    }
    to {
        transform: rotate(3deg);
        opacity: 0.5;
    }
}
@keyframes footer-left-limbs {
    from {
        transform: rotateX(30deg) rotateZ(2deg);
    }
    to {
        transform: rotate(10deg);
    }
}
@keyframes footer-right-limbs {
    from {
        transform: rotate(-10deg);
    }
    to {
        transform: rotateX(20deg) rotateZ(2deg);
    }
}
@keyframes footer-head {
    from {
        transform: translate(1159.598538px, 0px) rotate(0deg);
    }
    to {
        transform: translate(1159.598538px, 0px) rotateZ(3deg);
    }
}
@keyframes pin-floating-1 {
    from {
        transform: translate(-300%, -400%) rotate(0deg);
    }
    to {
        transform: translate(-300%, -420%) rotateX(5deg);
    }
}
@keyframes pin-floating-2 {
    from {
        transform: translate(-1000%, -600%) rotate(0deg);
    }
    to {
        transform: translate(-1000%, -620%) rotateZ(5deg);
    }
}
@keyframes pin-floating-3 {
    from {
        transform: translate(-1100%, -900%) rotate(0deg);
    }
    to {
        transform: translate(-1100%, -920%) rotateZ(-5deg);
    }
}
@keyframes pin-floating-4 {
    from {
        transform: translate(-1700%, -300%) rotate(0deg);
    }
    to {
        transform: translate(-1700%, -320%) rotateX(10deg);
    }
}
@keyframes pin-floating-5 {
    from {
        transform: translate(-1400%, -400%) rotate(0deg);
    }
    to {
        transform: translate(-1400%, -420%) rotateZ(3deg);
    }
}
@keyframes pin-floating-6 {
    from {
        transform: translate(-700%, -800%) rotateZ(4deg);
    }
    to {
        transform: translate(-700%, -820%) rotate(0deg);
    }
}
@keyframes pin-floating-7 {
    from {
        transform: translate(-600%, -300%) rotateZ(6deg);
    }
    to {
        transform: translate(-600%, -320%) rotate(0deg);
    }
}
.footer-cta {
    max-width: 440px;
    margin: 0 auto;
    margin-left: 12%;
    padding-top: 20%;
    text-align: center;
}
.footer {
    .para {
        color: @color_1;
        text-align: center;
        margin-top: 5px;
    }
    position: relative;
    #l-arm {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: footer-left-limbs 1.5s ease-in infinite alternate;
    }
    #l-leg {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: footer-left-limbs 1.5s ease-in infinite alternate;
    }
    #r-arm {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: footer-right-limbs 1.5s ease-in infinite alternate;
    }
    #r-leg {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: footer-right-limbs 1.5s ease-in infinite alternate;
    }
    #head {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: bottom;
        transform: translate(1159.598538px, 0px);
        animation: footer-head 1.5s ease-in infinite alternate;
    }
}
.bot-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .navigation {
        flex-grow: 2;
        margin: 0 auto;
        text-align: right;
        margin-right: 40px;
    }
    .logo {
        display: block;
    }
    .logo.ch {
        background: url(../images/logo_dark.svg) no-repeat center center;
        background-size: contain;
        height: 36px;
        width: 160px;
    }
    .logo.en {
        background: url(../images/logo_dark_en.svg) no-repeat center center;
        background-size: contain;
        height: 36px;
        width: 160px;
    }
}
