.footer-bg {
    width: 100%;
    height: 85%;
    background: url(../images/footer/footer-bg.svg) no-repeat right bottom;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    bottom: 0px;
    right: 0px;
    z-index: -2;
}
.footer-earnie {
    position: absolute;
    right: 0;
    bottom: 20%;
    width: 55%;
    animation: footer-in 4.5s ease-in-out;
}
#airpath {
    box-sizing: border-box;
    animation: footer-air 1.5s ease-in-out infinite alternate;
    transform-origin: left;
}
.floating-pins {
    width: 100%;
}
.pin {
    width: 50px;
    height: 50px;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: -1;
    &:nth-child(1) {
        background: url(../images/footer/products.svg) no-repeat right bottom;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        transform: translate(-300%, -400%);
        animation: pin-floating-1 1s ease-in-out infinite alternate;
    }
    &:nth-child(2) {
        background: url(../images/footer/coins.svg) no-repeat right bottom;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        transform: translate(-1000%, -600%);
        animation: pin-floating-2 1.5s ease-in-out infinite alternate;
    }
    &:nth-child(3) {
        background: url(../images/footer/analyze.svg) no-repeat right bottom;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        transform: translate(-1100%, -900%);
        animation: pin-floating-3 1.8s ease-in-out infinite alternate;
    }
    &:nth-child(4) {
        background: url(../images/footer/share.svg) no-repeat right bottom;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        transform: translate(-1700%, -300%);
        animation: pin-floating-4 1.2s ease-in-out infinite alternate;
    }
    &:nth-child(5) {
        background: url(../images/footer/save.svg) no-repeat right bottom;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        transform: translate(-1400%, -400%);
        animation: pin-floating-5 2s ease-in-out infinite alternate;
    }
    &:nth-child(6) {
        background: url(../images/footer/like.svg) no-repeat right bottom;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        transform: translate(-700%, -800%);
        animation: pin-floating-6 2s ease-in-out infinite alternate;
    }
    &:nth-child(7) {
        background: url(../images/footer/magic.svg) no-repeat right bottom;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        transform: translate(-600%, -300%);
        animation: pin-floating-7 1.7s ease-in-out infinite alternate;
    }
}
@media only screen and (max-width: 1024px) {
    .bot-nav {
        .logo.ch {
            background: url(../images/logo_light.svg) no-repeat center center;
            background-size: contain;
        }
        .logo.en {
            background: url(../images/logo_light_en.svg) no-repeat center center;
            background-size: contain;
        }
    }
}
@media only screen and (max-width: 414px) {
    .footer {
        display: grid;
        grid-template-columns: auto;
    }
    .footer-cta {
        margin: 0;
        padding: 0;
        align-self: center;
        justify-self: center;
    }
    .footer-earnie {
        width: 120%;
        bottom: 30%;
        z-index: -1;
        right: -50%;
    }
    .footer-bg {
        height: 70%;
        background-size: cover;
    }
    .bot-nav {
        display: none;
    }
}
