/************ *ABOUT SECTION* ************/
@keyframes about-bg {
  from {
    opacity: 0.9;
  }
  to {
    opacity: 1;
  }
}
/************ ABOUT SECTION ************/
@keyframes about-robot {
  from {
    transform: rotate(0deg) rotateZ(2deg);
  }
  to {
    transform: rotateX(10deg);
  }
}
@keyframes about-air {
  from {
    transform: rotate(0deg) rotateZ(-2deg);
    opacity: 1;
  }
  to {
    transform: rotateX(14deg);
    opacity: 0.5;
  }
}
@keyframes fly {
  0% {
    transform: translate(-400px, 200px) rotateZ(10deg);
  }
  50% {
    transform: translate(30px, -20px) rotateZ(-4deg);
  }
  100% {
    transform: translate(0px, 0px) rotateZ(0deg);
  }
}
@keyframes ab-left-limbs {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotateZ(7deg);
  }
}
@keyframes ab-right-limbs {
  from {
    transform: rotate(7deg);
  }
  to {
    transform: rotateZ(0deg);
  }
}
@keyframes ab-head {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotateZ(-5deg);
  }
}
