.about {
  position: relative;
  padding-bottom: 120px;
}
.about .svg-bg {
  width: 100%;
  height: 280px;
  background: url(../images/earnie/bg.svg) no-repeat top center;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  z-index: -1;
  animation: about-bg 3s ease-in-out infinite alternate;
}
.about .svg-earnie {
  width: 230px;
  align-self: center;
  justify-self: center;
  position: absolute;
  top: 15%;
  left: 30%;
}
.about .heading {
  margin-top: 40px;
  padding: 20px 0;
  text-align: center;
}
.about .para {
  max-width: 600px;
  margin: 0 auto;
}
.about .content {
  width: 80%;
}
.about #earnie {
  box-sizing: border-box;
  animation: about-robot 1.5s ease-in-out infinite alternate;
  transform-origin: bottom;
}
.about #air {
  box-sizing: border-box;
  animation: about-air 1.5s ease-in-out infinite alternate;
  transform-origin: top;
}
.about #earnie-flying {
  animation: fly 3s ease-in-out;
}
.about #l-arm {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: ab-left-limbs 1.5s ease-in infinite alternate;
}
.about #l-leg {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: ab-left-limbs 1.5s ease-in infinite alternate;
}
.about #r-arm {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: ab-right-limbs 1.5s ease-in infinite alternate;
}
.about #r-leg {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: ab-right-limbs 1.5s ease-in infinite alternate;
}
.about #head {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: bottom;
  animation: ab-head 1.5s ease-in infinite alternate;
}
@media only screen and (max-width: 414px) {
  .about .svg-bg {
    height: 100px;
  }
  .about .svg-earnie {
    display: none !important;
    width: 200px;
    top: 5%;
    left: 20%;
  }
  .about .heading {
    margin-top: 0;
  }
}
@media only screen and (max-width: 320px) {
  .about .para {
    grid-row-start: row5;
  }
}
