
@color-blue: #1652F0;
@color-red: #ff7f00;
@color-green: #4fbb92;
@color_1: #FFFFFF;
@color_2: #F6F9FF;
@color_3: #1652F0;
@color_4: #677382;
@color_5: #000000;
@font_family_1: "Noto Sans SC";
@font_family_2: "Open Sans";
@font_family_3: "Open Sans", sans-serif;
@background_color_1: #1c232f;
@background_color_2: #F3F5F7;
@background_color_3: #1652F0;
@background_color_4: #FFFFFF;
@border_color_1: #1652F0;

.container {
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 800px;
    position: relative;
}
.download {
    flex: 0 0 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 3%;
}
.download-btn {
    img {
        margin: 1em;
    }
}
.hero {
    background: url(../images/bg_hero.png) no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-color: @background_color_1;
    overflow: hidden;
}
.nav {
    color: @color_1;
}
.hero-content {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 auto;
    text-align: center;
    width: 80%;
}
.title {
    font-family: @font_family_1;
    font-weight: 300;
    color: @color_1;
    font-size: 3.125rem;
    .special-char {
        display: block;
        font-family: @font_family_1;
        font-weight: 900;
        font-size: 1.4em;
    }
}
.subheading {
    color: @color_2;
    font-size: 1rem;
    margin-top: 1.5em;
    flex: 0 0 100%;
    p {
        max-width: 540px;
        margin: 0 auto;
        text-align: center;
        line-height: 25px;
    }
}
.feature {
    display: block;
    text-align: center;
    padding-top: 1rem;
    min-height: 1000px !important;
    .heading {
        margin-top: 2%;
    }
    .para {
        max-width: 600px;
        margin: 20px auto 0;
        text-align: center;
    }
}
.sections {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    max-width: 1600px;
    margin: 80px auto;
}
.sect {
    min-height: 250px;
    padding: 30px;
    .para {
        max-width: 250px;
        text-align: left;
    }
}
.sect-title {
    font-family: @font_family_1;
    font-weight: 900;
    font-size: 1.5rem;
    color: @color_3;
    text-align: left;
    span {
        margin-right: 10px;
        img {
            max-height: 40px;
            vertical-align: middle;
        }
    }
}
.rect {
    width: 600px;
    min-height: 200px;
    margin: 0 auto;
    text-align: left;
    padding: 12px 40px 12px 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    background-color: @background_color_2;
    box-shadow: 0px 5px 10px rgba(120, 130, 147, 0.4);
    .content {
        margin: 0 auto;
        vertical-align: middle;
    }
    .heading {
        color: @color_3;
        font-size: 1.5rem;
        max-width: 530px;
        align-self: flex-start;
    }
    .para {
        text-align: left;
        max-width: 530px;
    }
}
.rect {
    width: 624px;
    min-height: 230px;
    padding: 12px 30px 22px 30px;
    .heading {
        max-width: 600px;
    }
    .para {
        max-width: 600px;
    }
}
.illustration-left {
    width: 100%;
    height: 30.47rem;
    background: url(../images/left_cloud_solution.png) no-repeat left center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    z-index: -2;
    transform: translate(0%, -70%);
}
.illustration-rocket {
    width: 100%;
    height: 11rem;
    background: url(../images/rocket_solution.png) no-repeat left center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    z-index: -1;
    transform: translate(5%, -100%);
}
.example {
    display: flex;
    justify-content: space-around;
    min-width: 960px;
    .content {
        width: 80%;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    .left {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        max-width: 25%;
        padding: 1.875em;
        border-radius: 4px;
        .para {
            margin-top: 0.625em;
        }
    }
    .right {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
    }
}
.illustration-right {
    width: 100%;
    height: 100%;
    background: url(../images/right_cloud_ex.png) no-repeat right center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    z-index: -2;
    transform: translate(0, 1.875em);
}
.image {
    width: 9.5em;
    height: 9.5em;
    display: inline-block;
    border-radius: 6px;
    border: 1.5px solid #FFFFFF;
    margin: 0.6em;
}
.new-box {
    padding: 120px 0;
}
.app {
    .content {
        width: 80%;
        margin: 0 auto;
    }
    .app-content {
        margin-top: 3%;
        display: none;
        .left {
            max-width: 782px;
            min-height: 260px;
            display: flex;
            box-shadow: 0 2px 10px rgba(60, 66, 99, 0.25);
        }
        .bar {
            width: 40px;
            min-height: 100%;
            background-color: @background_color_3;
            border-radius: 5px 0 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 0 10px 0 10px;
        }
    }
    .app-content.active {
        display: block;
    }
}
.row {
    max-width: 782px;
    margin: 0 auto;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10%;
    .left {
        .heading {
            span {
                font-size: 1.5rem;
                font-family: @font_family_1;
                font-weight: 300;
                color: @color_3;
                display: block;
            }
        }
    }
    .right {
        .download-now-btn {
            border-color: @border_color_1;
            color: @color_3;
            @media only screen and (max-width: 1800px) {
                border-color: #fff;
                color: #fff;
            }
            @media only screen and (max-width: 414px) {
                border-color: @border_color_1;
                color: @color_3;
            }
        }
    }
}
.container.app {
    padding-top: 5rem;
    min-height: 1000px;
    @media only screen and (max-width: 414px) {
        padding-top: 0;
        min-height: auto;
    }
}
.current-view {
    img {
        max-width: 40px;
        margin: 0 auto;
        vertical-align: middle;
    }
}
.app-content {
    .card {
        border-radius: 0 5px 5px 0;
        padding: 60px 40px 60px 40px;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
    }
}
.card {
    .view-title {
        font-family: @font_family_1;
        font-weight: 300;
        font-size: 2rem;
        color: @color_3;
        margin-bottom: 15px;
    }
}
.view-selector {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 5%;
}
.view-opt {
    font-family: @font_family_1;
    font-weight: 300;
    font-size: 1.25rem;
    color: @color_4;
    margin: 0 auto;
    text-align: center;
    &:hover {
        color: @color_3;
        cursor: pointer;
    }
    img {
        display: inline-block;
        height: 48px;
        margin-bottom: 10px;
    }
}
.view-opt.selected {
    color: @color_3;
}
.discover {
    .card {
        padding: 0;
        .view-title {
            width: 100%;
            padding: 60px 40px 0 40px;
        }
        ul {
            li {
                &:not(:first-child) {
                    display: none;
                }
            }
        }
    }
    .para {
        padding: 0 40px;
    }
    .tabs {
        display: flex;
        justify-content: space-around;
        width: 100%;
        align-self: flex-end;
    }
}
.tabs {
    .tab {
        font-family: @font_family_1;
        font-weight: 400;
        font-size: 1.25rem;
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }
}
.tab.selected {
    color: @color_3;
    &:hover {
        cursor: default;
    }
}
.tab {
    .underline {
        height: 5px;
        background-color: @background_color_3;
        margin-top: 5px;
    }
    &:not(.selected) {
        .underline {
            display: none;
        }
    }
    &:hover {
        color: @color_3;
        cursor: pointer;
        display: block;
        .underline {
            color: @color_3;
            cursor: pointer;
            display: block;
        }
    }
}
#left-cloud-test {
    width: 100%;
    height: 700px;
    background: url(../images/cloud_test.svg) no-repeat left center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    bottom: 0;
    left: -10%;
    top: -20px;
    z-index: -2;
}
.testimonials {
    display: flex;
    justify-content: space-around;
    align-items: center;
    max-width: 960px;
    margin: 0 auto;
    .left {
        min-width: 264px;
    }
    .heading {
        font-size: 1.5rem;
        color: @color_4;
    }
    .right {
        max-width: 540px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .content {
        width: 460px;
        padding: 40px;
        box-shadow: 0 4px 10px rgba(106, 107, 132, 0.3);
        border-radius: 5px;
        background-color: @background_color_4;
        display: none;
        flex-direction: column;
        justify-content: center;
        min-height: 440px;
        .para {
            width: 100%;
            color: @color_5;
        }
    }
    .content.active {
        display: flex;
    }
}
.tag {
    color: @color_1;
    padding: 0 20px;
    max-width: 150px;
    height: 40px;
    line-height: 40px;
    background-color: @background_color_3;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    text-align: center;
}
.author {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 20px;
    .info {
        margin-left: 12px;
    }
}
.profile {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    img {
        width: 80px;
        height: 80px;
        border-radius: 50%;
    }
}
.author-name {
    font-family: @font_family_1;
    font-weight: 400;
    font-size: 1.125rem;
    color: @color_3;
    margin-bottom: 5px;
}
.author-description {
    font-family: @font_family_1;
    font-weight: 300;
    font-size: 0.875rem;
    color: @color_4;
}
.test-controller {
    align-self: flex-end;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-top: 40px;
    background-color: @background_color_3;
    border-radius: 52.5px;
    padding: 10px;
    box-shadow: 0 1.5px 7px rgba(77, 89, 121, 0.5);
    .profile {
        width: 56px;
        height: 56px;
        opacity: 0.5;
        //border: 2px solid #1652F0;
        img {
            width: 56px;
            height: 56px;
        }
        &:hover {
            opacity: 1.0;
            //border: 2px solid #FFFFFF;
            cursor: pointer;
        }
    }
    .profile.selected {
        opacity: 1.0;
        //border: 2px solid #FFFFFF;
        &:hover {
            cursor: default;
        }
    }
}
@media only screen and (min-width: 768px) {
    .hero-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    .example {
        .content {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    .app {
        .content {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}
@media only screen and (max-width: 414px) {
    .container {
        min-height: auto;
        min-width: initial;
        width: 100%;
        height: auto;
        padding-bottom: 80px;
    }
    .new-box {
        padding: 40px 0;
    }
    .heading {
        font-size: 22px;
    }
    .hero-content {
        .title {
            font-size: 1.75rem;
            line-height: 2.5rem;
        }
    }
    .download {
        padding: 0 1rem;
    }
    .download-btn {
        flex-basis: 100%;
        padding: 0;
        margin: 0;
        margin-bottom: -1rem;
    }
    .para {
        padding: 0 1rem 0 1rem;
        text-align: left !important;
        font-size: 15px;
    }
    .sections {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        .sect {
            padding: 1rem;
            min-height: auto;
            .para {
                max-width: none;
                margin-top: 10px;
                padding: 0;
            }
        }
    }
    .feature {
        min-height: auto !important;
        .rect {
            width: auto;
            padding: 1rem;
        }
    }
    .rect {
        .content {

        }
        .heading {
            line-height: 26px;
        }
        .para {
            padding: 0;
        }
    }
    .example {
        flex-wrap: wrap;
        min-width: auto;
        padding-top: 5rem;
        .content {
            flex-wrap: wrap;
            width: auto;
            padding: 0 1rem 0 1rem;
        }
        .left {
            max-width: 100%;
            width: 100%;
            padding: 0;
            margin: 0;
            .para {
                padding: 0 1rem;
            }
        }
        .right {
            display: none;
            max-width: 100%;
            width: 100%;
            padding: 0;
            margin: 0;
            margin-top: 2rem;
            .row {
                max-width: 100%;
                width: 95%;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-around;
                margin-bottom: 0.625rem;
            }
        }
        .heading {
            padding: 0 1rem;
            width: 100%;
        }
        .illustration-right {
            transform: translate(0, 0);
            display: none;
        }
    }
    .right {
        .image {
            width: 6rem;
            height: 6rem;
            margin: 0;
            border: 1px solid #F3F5F7;
        }
    }
    .app {
        .header {
            flex-wrap: wrap;
            margin-bottom: 6rem;
            .left {
                width: 100%;
                padding: 0 1rem 0 1rem;
            }
            .right {
                width: 100%;
                padding: 0 1rem 0 1rem;
                margin-top: 0.625rem;
            }
        }
        .app-content {
            .bar {
                display: none;
            }
            .card {
                padding: 1rem;
                border-radius: 5px;
                min-height: 268px;
                .view-title {
                    margin-bottom: 0;
                }
                .para {
                    padding: 0;
                }
                .tabs {
                    .tab {
                        font-size: 14px;
                        .underline {
                            height: 2px;
                        }
                    }
                }
            }
            &.discover {
                .card {
                    .view-title {
                        padding: 0;
                    }
                }
            }
        }
        .view-selector {
            .view-opt {
                font-size: 14px;
                img {
                    height: 20px;
                    margin: 0 auto 4px;
                }
            }
        }
        .content {
            width: auto;
            padding: 0 1rem 0 1rem;
        }
    }
    .testimonials {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        .left {
            max-width: 100%;
            width: 100%;
            margin: 0 auto;
            text-align: center;
            padding: 1rem 0 1rem 0;
            .tag {
                text-align: center !important;
                margin: 0 auto;
            }
        }
        .right {
            max-width: 100%;
            width: 100%;
            .content {
                padding: 2rem 1rem 2rem 1rem;
            }
        }
        .content {
            .para {
                width: auto;
            }
        }
        .heading {
            padding: 1rem 0 1rem 0;
        }
        .author {
            flex-flow: row;
            .info {
                flex: 1;
            }
        }
    }
    .test-controller {
        margin-top: 10px;
    }
    .author {
        width: auto;
    }
    .illustration-bottom {
        display: none;
    }
    .footer-cta {
        padding: 0;
        margin: 0 auto;
        width: 100%;
        margin-bottom: 160px;
        .para {
            text-align: center !important;
        }
    }
    .bot-nav {
        color: @color_5;
    }
    .hero {
        .hero-content {
            width: auto;
            padding: 0 1rem 0 1rem;
        }
    }
    .about {
        .content {
            width: auto;
            padding: 0 1rem 0 1rem;
        }
    }
}
