.about {
    position: relative;
    padding-bottom: 120px;
    .svg-bg {
        width: 100%;
        height: 280px;
        background: url(../images/earnie/bg.svg) no-repeat top center;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        z-index: -1;
        animation: about-bg 3s ease-in-out infinite alternate;
    }
    .svg-earnie {
        width: 230px;
        align-self: center;
        justify-self: center;
        position: absolute;
        top: 15%;
        left: 30%;
    }
    .heading {
        margin-top: 40px;
        padding: 20px 0;
        text-align: center;
    }
    .para {
        max-width: 600px;
        margin: 0 auto;
    }
    .content {
        width: 80%;
    }
    #earnie {
        box-sizing: border-box;
        animation: about-robot 1.5s ease-in-out infinite alternate;
        transform-origin: bottom;
    }
    #air {
        box-sizing: border-box;
        animation: about-air 1.5s ease-in-out infinite alternate;
        transform-origin: top;
    }
    #earnie-flying {
        animation: fly 3s ease-in-out;
    }
    #l-arm {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: ab-left-limbs 1.5s ease-in infinite alternate;
    }
    #l-leg {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: ab-left-limbs 1.5s ease-in infinite alternate;
    }
    #r-arm {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: ab-right-limbs 1.5s ease-in infinite alternate;
    }
    #r-leg {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: top;
        animation: ab-right-limbs 1.5s ease-in infinite alternate;
    }
    #head {
        box-sizing: border-box;
        transform-box: fill-box;
        transform-origin: bottom;
        animation: ab-head 1.5s ease-in infinite alternate;
    }
}

@media only screen and (max-width: 414px) {
    .about {
        .svg-bg {
            height: 100px;
        }
        .svg-earnie {
            display: none !important;
            width: 200px;
            top: 5%;
            left: 20%;
        }
        .heading {
            margin-top: 0;
        }
    }
}

@media only screen and (max-width: 320px) {
    .about {
        .para {
            grid-row-start: row5;
        }
    }
}
