/*********** *SVG* *************/
/********** bottom part ************/
/******* map dots *********/
@keyframes bg-animation {
    0% {
        background-position: left;
    }
    100% {
        background-position: right;
    }
}
@keyframes light {
    from {
        opacity: 0.5;
    }
    to {
        opacity: 1.0;
    }
}
@keyframes left-limbs {
    from {
        transform: rotate(-20deg);
    }
    to {
        transform: rotate(10deg);
    }
}
@keyframes right-limbs {
    from {
        transform: rotate(20deg);
    }
    to {
        transform: rotate(-10deg);
    }
}
@keyframes fly-in {
    0% {
        transform: translate(-300px, 250px) rotateZ(10deg);
    }
    50% {
        transform: translate(30px, -50px) rotateZ(-4deg);
    }
    100% {
        transform: translate(0px, 0px) rotateZ(0deg);
    }
}
@keyframes fly-out {
    from {
        transform: translate(0px, 0px) rotateZ(-5deg);
    }
    to {
        transform: translate(0px, -1400px) rotateZ(0deg);
    }
}
@keyframes robot {
    from {
        transform: translate(1714px, 1402px) rotate(0deg);
    }
    to {
        transform: translate(1694px, 1410px) rotateX(14deg);
    }
}
@keyframes rocket {
    from {
        transform: rotate(0deg);
        opacity: 0.9;
    }
    to {
        transform: translate(-20px, 8px) rotateX(14deg);
        opacity: 0.5;
    }
}
@keyframes play {
    0% {
        transform: translate(1414px, 1402px) rotate(10deg);
    }
    33% {
        transform: translate(1514px, 1452px) rotate(2deg);
    }
    66% {
        transform: translate(1814px, 1302px) rotate(-10deg);
    }
    100% {
        transform: translate(1714px, 1402px) rotate(1deg);
    }
}
@keyframes shining {
    0% {
        width: 0;
    }
    50% {
        width: 12px;
    }
    100% {
        width: 0;
    }
}
@keyframes title-in {
    0% {
        opacity: 0;
        transform: translateY(-30px);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}
