.hero {
  overflow: hidden;
  background: #1c232f url(../images/hero/bg.svg) no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.hero::before {
  content: '';
  background-image: var(--gradient);
  background-size: 300%;
  animation: bg-animation 6s ease-in-out infinite alternate;
  height: 100%;
  position: absolute;
  z-index: -98;
  top: 0;
  left: 0;
  right: 0;
  mix-blend-mode: overlay;
}
.hero-content {
  width: 100%;
  display: grid;
  grid-template-columns: auto;
  position: relative;
  height: inherit;
}
.img-wrapper {
  width: 100%;
  align-self: center;
  justify-self: center;
  z-index: -90;
  position: absolute;
}
#worldmap {
  width: 100%;
  animation: light 2s ease-in-out infinite alternate;
}
#lines {
  animation: light 2s ease-in-out infinite alternate;
}
#earnie {
  box-sizing: border-box;
  transform: translate(1714px, 1402px);
  animation: robot 1.5s ease-in-out infinite alternate;
  transform-origin: bottom;
}
#air {
  box-sizing: border-box;
  animation: rocket 1.5s ease-in-out infinite alternate;
  transform-origin: top;
}
#l-arm {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: left-limbs 1.5s ease-in infinite alternate;
}
#l-leg {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: left-limbs 1.5s ease-in infinite alternate;
}
#r-arm {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: right-limbs 1.5s ease-in infinite alternate;
}
#r-leg {
  box-sizing: border-box;
  transform-box: fill-box;
  transform-origin: top;
  animation: right-limbs 1.5s ease-in infinite alternate;
}
.star-container {
  width: 100%;
  align-self: end;
  justify-self: center;
  height: 100%;
}
.star {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1px;
  height: 1px;
  border-radius: 50%;
  background: linear-gradient(-45deg, #A3D3FF, rgba(0, 0, 255, 0));
  filter: drop-shadow(0 0 6px #699BFF);
}
.star::before {
  position: absolute;
  content: '';
  top: calc(50% - 1px);
  right: 0;
  height: 2px;
  width: 12px;
  background: linear-gradient(-45deg, rgba(0, 0, 255, 0), #A3D3FF, rgba(0, 0, 255, 0));
  border-radius: 100%;
  transform: translateX(50%) rotateZ(45deg);
  animation: shining 2s ease-in-out infinite;
}
.star::after {
  position: absolute;
  content: '';
  top: calc(50% - 1px);
  right: 0;
  height: 2px;
  width: 12px;
  background: linear-gradient(-45deg, rgba(0, 0, 255, 0), #A3D3FF, rgba(0, 0, 255, 0));
  border-radius: 100%;
  transform: translateX(50%) rotateZ(45deg);
  animation: shining 2s ease-in-out infinite;
  transform: translateX(50%) rotateZ(-45deg);
}
.star:nth-child(1) {
  top: calc(50% - 4%);
  left: calc(50% - 26.5%);
  animation-delay: 650ms;
}
.star:nth-child(2) {
  top: calc(50% - 7%);
  left: calc(50% - 29.5%);
  animation-delay: 650ms;
}
.star:nth-child(3) {
  top: calc(50% - 2%);
  left: calc(50% - 39.2%);
  animation-delay: 650ms;
}
.star:nth-child(4) {
  top: calc(50% + 8%);
  left: calc(50% - 25.6%);
  animation-delay: 650ms;
}
.star:nth-child(5) {
  top: calc(50% - 1.5%);
  left: calc(50% + 34.6%);
  animation-delay: 650ms;
}
.star:nth-child(6) {
  top: calc(50% - 8%);
  left: calc(50% - 3%);
  animation-delay: 650ms;
}
.star:nth-child(7) {
  top: calc(50% - 6%);
  left: calc(50% + 22.5%);
  animation-delay: 650ms;
}
.star:nth-child(8) {
  top: calc(50% - 2%);
  left: calc(50% + 27%);
  animation-delay: 650ms;
}
.title-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 auto;
  text-align: center;
  width: 80%;
  margin-top: 15%;
  z-index: 100;
  animation: title-in 1.5s ease-in-out;
}
.ch .title-wrapper {
  margin-top: 12%;
}
.nav {
  color: #FFFFFF;
}
.title {
  font-family: "Noto Sans SC";
  font-weight: 300;
  color: #FFFFFF;
}
.title.ch {
  font-size: 3.125rem;
  flex: 0 0 100%;
  line-height: 70px;
}
.title.ch .special-char {
  display: block;
  font-family: "Noto Sans SC";
  font-weight: 900;
  font-size: 1.4em;
}
.subheading {
  color: #F6F9FF;
  font-size: 1rem;
  margin-top: 1.5em;
  flex: 0 0 100%;
}
.subheading p {
  max-width: 540px;
  margin: 0 auto;
  text-align: center;
  line-height: 25px;
}
@media only screen and (max-width: 414px) {
  .container {
    min-width: auto;
    height: auto;
    min-height: 600px;
  }
  .container.hero {
    height: 100%;
  }
  .container.filler {
    height: 100%;
  }
  .title-wrapper {
    grid-row: 3;
  }
  .title-wrapper .title {
    font-size: 1.45em;
    line-height: inherit;
  }
  .title-wrapper .subheading {
    margin-top: 1em;
    font-size: 1em;
  }
  #foreground {
    width: 150%;
  }
  .download-btn img {
    height: 48px;
  }
  .feature {
    height: initial;
  }
}
@media only screen and (max-width: 320px) {
  .title-wrapper .title {
    font-size: 1em;
  }
}
